  font-size: 12px;
  line-height: 1.5em;
  margin: 0; }

h6 {
  font-size: 10px;
  line-height: 1.5em;
  margin: 0; }

*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.container {
  max-width: 1200px;
  padding: 10px;
  width: 96%;
  margin: 0 auto; }

.row {
  margin-right: -10px;
  margin-left: -10px; }
  .row:after, .row:before {
    content: "";
    display: table;
    clear: both; }

[class*=col-] {
  width: 100%;
  float: left;
  padding: 0 10px; }

